.table-container {
  /* justify-content: center; */
  margin-top: 50px;
}

.cashflow_table {
  border-collapse: collapse;
  margin-top: 20px;
  margin-bottom: 100px;
  margin-right: 60px;
  margin-left: 60px;
  font-size: 14px;
  font-family: sans-serif;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  width: 90%;
  min-width: 500px;
  animation: slideIn 1.5s ease-in-out forwards;
}

.cashflow_table thead tr {
  background-color: #97A97C;
  color: #ffffff;
  text-align: left;
}

.cashflow_table th,
.cashflow_table td {
  padding: 10px 15px;
}

.cashflow_table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.cashflow_table tbody tr:nth-of-type(even) {
  background-color: #f6f6f6;
}

.cashflow_table tbody tr:last-of-type {
  border-bottom: 2px solid #97A97C;
}

.cashflow_table tbody tr.active-row {
  font-weight: bold;
  color: #97A97C;
}


/* .cashflow_table td:nth-child(2) input {
  width: 120px;
} */

.cashflow_table button {
  font-size: 20px;
  border-radius: 60px;
  height: 40px;
  width: 40px;
  border: 1px solid #ccc;
  color: #FFACC5;
}
.cashflow_table button:hover {
  background-color: #c2c2c2;
}

.cashflow_table button[name="cancel"] {
  color: #ec2e2e;
}

.cashflow_table button[name="done"] {
  color: #4bbd7f;
}

table input,
select {
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 25px;
  /* padding: 2px; */
}

table .edit-cell {
  gap: 5px;
  text-align: right;
  margin-right: 20px;
}
.cashflow_table tr td:first-child { display: none; }
.cashflow_table tr th:first-child { display: none; }
.styled-header {
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  margin-left: 2%;
}

.table-button {
  padding: 0;
  margin-left: 4px;
}
.spinner {
  margin-left:50%;
  margin-top: 10%;
}
@keyframes slideIn {
  0% {
    transform: translateX(500px) scale(.2);
  }
  100% {
    transform: translateX(0px) scale(1);
  }
}