.navbar {
    height: 60px;
    border: 1px solid #374028;
    background-color: #718355;
    display: flex;
    padding-left: 10px;
    min-width: 930px;
    
}

.navItem {
    color: #FFF0F3;
    display: inline-block;
    padding-right: 30px;
    padding-left: 30px;
    text-decoration: none;
}
.navItem header {
    padding-top: 15px;
    color: #fffbfc;
    font-size: x-large;
    font-weight: 1000;
    font-family: "Comic Sans MS", "Comic Sans", cursive;
}
.navButton {
    padding-top: 20px;
}
.navButton:hover {
    background-color: #B5C99A;
    color: #800F2F
}

.buttonsDiv {
    margin-left: auto;
    margin-right: 20px;
    margin-top: auto;
    margin-bottom: auto;
}